@media (max-width: @tablet-width-max) {
  .main-nav--closed {
    display: none;
  }
}

.main-nav__button {
  display: flex;
  cursor: pointer;
  background-color: transparent;
  border: none;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  padding: 0;
  align-items: center;
  justify-content: center;
  color: white;
  fill: currentColor;

  &--hidden {
    display: none;
  }

  &--closed {
    width: 50px;
    height: 30px;
  }

  &--opened {
    width: 25px;
    height: 25px;
  }

  &:hover {
    color: @pink;
  }

  &:active {
    color: rgba(210, 41, 86, 0.3);
  }
}

.main-nav__link {
  box-sizing: border-box;
  display: block;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 -1px 0 0 rgb(75, 86, 92), 0 -2px 0 0 rgb(0, 0, 0);
  padding: 16px 0;
  color: @bg-color--1;
  font-family: @font--bold;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 18px;
  line-height: 32px;

  &--last-one {
    border-bottom: 1px solid rgb(0, 0, 0);
  }

  &:hover {
    color: @pink;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    display: block;
  }

  .main-nav__button {
    display: none;
  }

  .main-nav__item {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  .main-nav__link {
    text-transform: none;
    padding: 10px 0;
    font-family: @font--main;
    font-weight: 400;
    box-shadow: none;

    &:hover {
      color: @pink;
    }

    &:active {
      color: @bg-color--1;
      opacity: 0.3;
    }

    &--active {
      border-bottom: 2px solid rgb(255, 255, 255);
    }

    &--last-one {
      border-bottom: none;
    }
  }

  .main-nav__list {
    height: 110px;
    width: 620px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
}
